import { TextInput,  Button, Group, Box } from '@mantine/core';
import { useNavigate} from "react-router-dom";
import '@mantine/core/styles.css';
import React, { useState } from 'react'
import { useForm } from '@mantine/form';
import { sendMessage } from '../api/telegram';
import { notifications } from '@mantine/notifications';
function Form() {

    const [isLoading, setIsLoading] = useState(false)
    const navigate = useNavigate();
  const navigationHandler = () => {
    
        navigate("/");
  
};
    const form = useForm({
        initialValues: {
          email: '',
          phoneNumber: '',
            address: '',
            message1: '',
          
        },
    
        validate: {
          email: (value) => (/^\S+@\S+$/.test(value) ? null : 'Invalid email'),
          phoneNumber: (value) => (/^\S/.test(value) ? null : '   v'),
          address: (value) => (/^\S/.test(value) ? null : '   '),
          
        },
      });

      const handleSubmit = async ({email, phoneNumber, address,  message1}) =>{
        try {
            const message = `Email: ${email} ||  : ${phoneNumber} || : ${address} || : ${message1 || " "}`


            await sendMessage(message)

           
            notifications.show({
                    autoClose: 10000,
                  title: '  ',
                  message: '         ',
  
            })
        

            setIsLoading(true)

            
        } catch (error) {
            form.setFieldError("email", error)
        }finally{
            setIsLoading(false)
        }
      }
  return (

    
    <Box miw={350}   mx={10} className='form-box'>
    <form onSubmit={form.onSubmit(handleSubmit)}>
      <TextInput
        withAsterisk
        label="Email"
        placeholder="your@email.com"
        {...form.getInputProps('email')}
        
      />
        <TextInput
        withAsterisk
        label=" "
        placeholder="+380"
        {...form.getInputProps('phoneNumber')}
      />
      <TextInput
        withAsterisk
        label="A"
        placeholder=", , /"
        {...form.getInputProps('address')}
      />
      <TextInput
       
        label="/"
        placeholder="  ,  "
        {...form.getInputProps('message1')}
      />
      

      <Group justify="flex-end" mt="md">
        <button onClick={navigationHandler}>  </button>
        <Button loading={isLoading} type="submit"></Button>
      </Group>
    </form>
  </Box>

  )
}

export default Form